.App {
  font-family: sans-serif;
  text-align: center;
  box-sizing: border-box;
  min-height: 100vh;
}
.container {
  box-sizing: border-box;
  border: 2px solid #a248f7;
  background-color: rgb(204, 255, 123);
  padding: 100px;
  margin: 100px;
  margin-left: 400px;
  margin-right: 400px;
}
.row {
  display: inline-block;
  padding-left: 20px;
}
h2 {
  padding-bottom: 20px;
}
.rowCal {
  margin-top: 20px;
  border-radius: 100px;
}
